@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap');

/*
  1. Use a more-intuitive box-sizing model.
*/
*, *::before, *::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  3. Allow percentage-based heights in the application
*/
html, body {
  height: 100%;
}
/*
  Typographic tweaks!
  4. Add accessible line-height
  5. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
  6. Improve media defaults
*/
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}
/*
  7. Remove built-in form typography styles
*/
input, button, textarea, select {
  font: inherit;
}
/*
  8. Avoid text overflows
*/
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}
/*
  9. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}
/*
  10. Avoid pointless scrolling and overflow issues
*/
html, body {margin: 0; height: 100%; overflow: auto}




  /* Variables */

:root {
    --clr-light-cyan: hsl(193, 38%, 86%);
    --clr-neon-green: hsl(150, 100%, 66%);                           /* Primary Colors */

    --clr-gray-blue: hsl(217, 19%, 38%);                            /* Neutral Colors */
    --clr-dark-gray-blue: hsl(217, 19%, 24%);
    --clr-dark-blue:  hsl(218, 23%, 16%);
    
    
    font-family: 'Manrope', sans-serif;
    font-size: 28px;
    font-weight: 800;
}

body {
  margin: 0 auto;
  background-color: var(--clr-dark-blue);
  display: flex;
  align-items: center;
  align-content: center;
}

h1 {
  color: var(--clr-neon-green);
  font-size: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.15rem;
}

p {
  color: var(--clr-light-cyan);
  margin-top: 1rem;
}

.card {
  margin: 0 auto;
  width: 17rem;
  height: 14rem;
  background-color: var(--clr-dark-gray-blue);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  border-radius: 0.3rem;
}

button {
  background-image: url(./assets/icon-dice.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-color: var(--clr-neon-green);
  height: 1rem;
  width: 1rem;
  padding: 1rem;
  border-radius: 50%;
  border-width: 0;
  position: absolute;
  top: 13rem;
  cursor: pointer;
}

button:hover {
  box-shadow: 1px 1px 8px 5px var(--clr-neon-green);
}

picture {
  margin-top: 1rem;
  width: 85%;
}

.attribution {
  margin-top: 8rem;
  font-size: 0.6rem;
  color: var(--clr-light-cyan);
}

.attribution a {
  color: var(--clr-neon-green);
}

@media (min-width: 786px) {
  .card {
    width: 18rem;
  }

  picture {
    width: 100%;
  }
}